header {
  background-color: $primary-page-color;
  padding-top: 12px;
  padding-bottom: 12px;

  @media screen and (min-width: $desktop) {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}
.logo__link {
  width: 40px;
  height: auto;
  @media screen and (min-width: $tablet) {
    width: 48px;
    height: auto;
  }

  @media screen and (min-width: $desktop) {
    width: 60px;
    height: auto;
  }
}

.header-container {
  display: flex;
  align-items: center;
  @media screen and (min-width: $desktop) {
    align-items: baseline;
  }
}

.nav {
  display: none;

  @media screen and (min-width: $desktop) {
    display: block;
    margin-left: 308px;
  }
}

.nav__list {
  @include flex_grid(4, 28px);
  font-weight: 700;
  font-size: 16px;
  line-height: calc(20px / 16px);
}

.nav__link {
  color: $secondary-text-color;
  font-weight: 700;

  transition: color $anim-std;

  &:hover,
  &:focus {
    color: $accent-color;
  }
}

.nav__btn {
  display: none;
  border: none;

  @media screen and (min-width: $tablet) {
    display: inline-flex;
    gap: 4px;
    align-items: center;
    padding: 12px 36px;
    margin-left: 32px;
  }

  @media screen and (min-width: $desktop) {
    margin-left: auto;
  }
}

.buy-now-icon {
  fill: currentColor;
}

// MOBILE MENU

.mobile-menu {
  padding-bottom: 64px;
  text-align: center;

  &__top {
    display: flex;
    align-items: center;
  }

  &__logo {
    display: block;
    width: 48px;
    height: 44px;
    margin-top: 12px;
    margin-left: 32px;
    @media screen and (min-width: $tablet) {
      margin-top: 20px;
    }
  }
  &__btn {
    border: none;
    display: flex;
    align-items: center;
    gap: 4px;
    margin-right: 20px;
    margin-top: 16px;

    @media screen and (min-width: $tablet) {
      margin-right: 32px;
      margin-top: 32px;
    }
    transition: color 0 cubic-bezier(0,1.02,0,1.02);
 

    &:hover,
    &:focus {
      color: $accent-color;
      background-size: 70px 70px;
      background-color: #7a7a7a40;
      border-radius: 50%;
    }

  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;

 

    @media screen and (min-width: $tablet) {
      gap: 40px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 32px;
    line-height: calc(39px / 32px);

    color: $secondary-text-color;
    transition: color $anim-std 250ms;

    @media screen and(min-width: $tablet) {
      font-size: 48px;
      line-height: calc(59px / 48px);
    }
    &:hover,
    &:focus {
      color: $primary-text-color;
    }
  }

  &__icon {
    fill: currentColor;
  }
}
.mobile-menu_wrapper {
  height: 100%;
  padding: 52px 0 64px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media screen and (orientation: landscape) {
  // ================
  // orientation landscape for mobile
  @media screen and (min-width: $mobile) {
    .mobile-menu_wrapper {
      height: 100%;
      padding-top: 0px;
      padding-bottom: 16px;
    }
    .header-container {
      max-width: 100%;
    }
    .mobile-menu__list{
       gap: 10px;
      padding-bottom: 10px;
    }
  }
  


    // ===========
    // orientation landscape for tablet
    @media screen and (min-width: $tablet) {
      .mobile-menu_wrapper{
        padding-top: 2vh;
      }
      .mobile-menu__list{
        gap: 10px;
       padding-bottom: 10px;
     }
        
    }


    // =============
    // orientation landscape for desktop
    @media screen and (min-width: $tablet){
      .header-container {
        max-width: 1200px;
      }

    }

  

  }


.menu-toggle {
  @include flex_centering(inline-flex);
  min-height: 32px;
  min-width: 32px;

  margin-left: auto;

  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;

  color: #fff;

  transition: color o cubic-bezier(0,1.02,0,1.02);

  @media screen and (min-width: $desktop) {
    display: none;
  }
  &:hover,
  &:focus {
    color: $accent-color;
    background-size: 70px 70px;
    background-color: #7a7a7a40;
    border-radius: 50%;
  }
}

.menu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  // height: 100vh;
  height: calc(100vh - calc(100vh - 100%));

  z-index: 999;

  transform: translateX(100%);
  transition: transform $anim-std;
  background-color: $primary-page-color;

  @media screen and (min-width: $tablet) {
    background-color: #97af8d;
  }
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu__btn--buy-now {
  display: flex;
  align-items: center;
  gap: 4px;
  width: 152px;
  padding: 9px 0px;

  @media screen and (min-width: $tablet) {
    display: none;
  }
}

.buy-now-icon {
  fill: currentColor;
}
